.todo-component {
  background: #fff9eb;
  list-style-type: none;
  border-radius: 25px;
  padding: 15px;
  color: #0d253f;
  font-family: Chewy;
  font-size: 20px;
  display: flex;
  justify-content: space-around;
  margin: 15px auto;
  width: 80%;
}
.todo-text {
  display: flex;
  max-width: 70%;
  text-align: center;
  align-items: center;
}
.todo-text:hover {
  color: #F9BE7C;
}

.todo-button {
  border: none;
  background: none;
  font-family: Chewy;
  font-size: 20px;
  font-weight: 1000;
}